
import Context from "./Context/index.vue";
import Block from "./Block/index.vue";
import Notes from "@/components/Notes/index.vue";
import Flag from "@/components/Flag.vue";

import { computed, defineComponent, PropType, reactive, ref, watch } from "vue";

import { Phrase, BlockSnapshot } from "./types";
import player from "@/audio-player";

export default defineComponent({
  components: { Context, Block, Notes, Flag },
  props: {
    lect: { type: String, default: "" },
    phrase: { type: Object as PropType<Phrase>, default: undefined },
  },
  setup(props) {
    const glossed = ref(false);
    const vblocks = reactive([] as BlockSnapshot[]);

    const text = computed(() =>
      vblocks
        .filter(({ state }) => state)
        .map(({ text }) => text)
        .join(" ")
    );
    function copy() {
      navigator.clipboard.writeText(text.value);
    }

    const playing = computed({
      get: () => player.key === props.lect,
      set: (p) => {
        if (p)
          player.play(
            props.lect,
            vblocks
              .map(({ state }) => state?.texts.map(({ plain }) => plain))
              .map((p) => "phrasebook/" + (p?.join("") ?? ""))
          );
        else player.stop();
      },
    });

    const seekers = ref([] as string[]);
    watch(
      [() => player.seek, () => player.current, playing],
      ([seek, current, playing], [, previous]) => {
        if (previous) seekers.value[previous.i] = "0";
        if (playing && current) seekers.value[current.i] = seek * 100 + "%";
      }
    );

    return { glossed, vblocks, copy, playing, seekers };
  },
});
